.advantages {
  width: 100%;
  margin: 0 auto;
  padding: 64px 350px 105px;

  @include vp-1439 {
    padding: 60px 250px;
  }

  @include vp-1279 {
    padding: 60px 140px;
  }

  @include vp-1023 {
    padding: 44px 0;
  }

  @include vp-767 {
    padding: 66px 0;
  }

  @include vp-374 {
    padding: 25px 0 21px 0;
  }
}

.advantages__wrapper {
  max-width: 1220px;
  margin: 0 auto;

  @include vp-1279 {
    width: 100%;
    margin: 0 auto;
  }

  @include vp-1023 {
    width: 100%;
    padding: 0 50px;
    margin: 0 auto;
  }

  @include vp-767 {
    width: 100%;
    padding: 0 25px;
    box-sizing: border-box;
    margin: 0 auto;
  }
}


.advantages__list {
  display: grid;
  grid-template-columns: repeat(2, minmax(auto, 595px));
  grid-template-rows: repeat(2, 300px);
  gap: 30px 32px;
  margin: 0;
  padding: 0;
  list-style-type: none;

  @include vp-1439 {
    grid-template-columns: repeat(auto-fit, minmax(320px, auto));
    grid-template-rows: repeat(auto-fit, minmax(300px, auto));
  }

  @include vp-1279 {
    grid-template-columns: repeat(auto-fit, minmax(320px, auto));
    grid-template-rows: repeat(auto-fit, minmax(303px, auto));
  }

  @include vp-1023 {
    grid-template-columns: repeat(auto-fit, minmax(320px, auto));
    grid-template-rows: repeat(2, 359px);
    gap: 30px 30px;
  }

  @include vp-767 {
    grid-template-rows: repeat(4, 340px);
    gap: 30px;
    grid-template-columns: 100%;
  }
}

.advantages__item {
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 40px;
  border-top: 1px solid #ebebeb;
  border-right: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  box-sizing: border-box;

  @include vp-1439 {
    gap: 10px;
  }

  @include vp-1279 {
    gap: 18px;
  }

  @include vp-767 {
    gap: 13px;
    padding: 25px;
  }
}

.advantages__item:hover,
.advantages__item:hover .advantages__icon,
.advantages__item:hover h3 {
  background-color: #00b2ff;
  fill: #f2f5f7;
  color: #f2f5f7;
}

.advantages__item--current {
  background-color: #00b2ff;
  color: #f2f5f7;
}

.advantages__item--current .advantages__title--current {
  color: #f2f5f7;
}

.advantages__item h3 {
  order: 2;
  color: #00b2ff;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  margin: 0;
  padding: 0;

  @include vp-767 {
    font-size: 20px;
    line-height: 140.62%;
  }
}

.advantages__icon {
  fill: #00b2ff;
  order: 1;
}

.advantages__icon--current {
  fill: #f2f5f7;
}

.advantages__item p {
  order: 3;
  font-family: "PT_Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  margin: 0;
  padding: 0;

  @include vp-1023 {
    font-size: 16px;
    line-height: 135%;
  }

  @include vp-767 {
    font-size: 14px;
  }
}
